<template>
    <v-container>
        <v-row class="mt-5" justify="center">
            <v-col cols="12">
                <v-text-field
                        v-model="search"
                        label="Поиск"
                        prepend-inner-icon="mdi-magnify"
                        outlined
                        required
                ></v-text-field>
                <v-simple-table v-if="news">
                    <thead>
                    <tr>
                        <th class="text-left">Заголовок</th>
                        <th class="text-left">Текст</th>
                        <th class="text-left">Дата</th>
                        <th></th>
                    </tr>
                    </thead>
                    <tbody>
                    <tr v-for="article in news_filtered" :key="article.id">
                        <td>{{article.title}}</td>
                        <td>{{article.text.length > 30 ? article.text.substring(0,30)+"..." : article.text}}</td>
                        <td>{{new Date(article.date).toLocaleDateString()}}</td>
                        <td><RouterLink :to="`/news/${article.id}`"><v-icon>mdi-account-edit</v-icon></RouterLink></td>
                    </tr>
                    </tbody>
                </v-simple-table>
                <v-btn to="/news/add" class="primary mt-5">Добавить новость</v-btn>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
    export default {
        name: "News",
        data() {
            return {
                search: '',
                news: null
            }
        },
        mounted() {
          this.getNews()
        },
        computed: {
            news_filtered: function() {
                return this.search ? this.news.filter(user => user.title.toLowerCase().includes(this.search.toLowerCase())) : this.news
            }
        },
        methods: {
            async getNews() {
                const res = await this.axios({url: `${process.env.VUE_APP_SERVER_URL}api/v1/news`, method: 'GET'})
                this.news = res.data
            }
        }
    }
</script>

<style scoped>
    a {
        text-decoration: none;
    }
    i {
        transition: all .3s;
        opacity: 0;
        cursor: pointer;
    }
    tr:hover i {
        opacity: 1;
    }
</style>